import test from 'ava';

let java = require("java");
java.classpath.push("class");
java.classpath.push("gson-2.8.0.jar");
let Main = java.import('com.bazaarvoice.Main');
let main;

test.beforeEach(t => {
    main = new Main();
});



test.serial('foo ',  t => {
    console.log("\nTest 1 started");

    let res1 = java.callMethodSync(main, "searchResults", "text", "sample");
    console.log("***"+res1+"***");
    t.is(res1,3,"ERROR!!!");

});


test.serial('Soo ',  t => {
    console.log("\nTest 2 started");

    let res2 = java.callMethodSync(main, "searchResults", "text", "hello");
    console.log("***"+res2+"***");
    t.is(res2,0,"ERROR!!!");

});


test.serial('Roo ', t => {
    console.log("\nTest 3 started");

    let res3 = java.callMethodSync(main, "searchResults", "word", "green");
    console.log("***"+res3+"***");
    t.is(res3,2,"ERROR!!!");

});
